import React from 'react';

import { ContactBox, MainContentBox } from '../../components';
import SLOnStage from './images/IMG_6574.jpg';
import MMWedding from './images/IMG_6578.jpg';
import Cert1 from './certs/cert1.png';
import Cert2 from './certs/cert2.jpg';
import Cert3 from './certs/cert3.jpg';
import SarimLogo from './images/sarim.gif';
import {
  AboutBox,
  AboutArticle,
  AboutImg,
  AboutParagraph,
  AboutSection,
  RowArticle,
  LidlLogo,
  CasionosLogo,
  OnTracoLogo,
  WarsawLogo,
  CertImg
} from './styled';

export const About = () => {
  return (
    <MainContentBox>
      <h1>O nas</h1>
      <AboutBox>
        <AboutArticle>
          <AboutImg src={SLOnStage} alt="SaturdayLive na scenie" />
          <div>
            <AboutParagraph>
              SaturdayLive to szecio-osobowy kolektyw tworzony przez
              utalentowanych i&nbsp;dowiadczonych wykonawcw. Zesp wyrnia
              si tym, e skada si wycznie z&nbsp;profesjonalnych,
              wyksztaconych muzykw, dla ktrych muzyka to nie tylko pasja, ale
              rwnie wyuczony zawd.
            </AboutParagraph>
            <AboutParagraph>
              Zesp powsta w&nbsp;2014 roku. Zwiedzilimy ca Polsk,
              koncertowalimy rwnie w&nbsp;Irlandii i&nbsp;Irlandii Pnocnej.
              Zaufay nam dziesitki klientw - firm, agencji, oraz osb
              prywatnych.
            </AboutParagraph>
          </div>
        </AboutArticle>

        <AboutArticle>
          <AboutImg src={MMWedding} alt="SaturdayLive na weselu" />{' '}
          <div>
            <AboutParagraph>
              Zesp powsta w&nbsp;2014 roku. Zwiedzilimy ca Polsk,
              koncertowalimy rwnie w&nbsp;Irlandii i&nbsp;Irlandii Pnocnej.
              Zaufay nam dziesitki klientw - firm, agencji, oraz osb
              prywatnych.
            </AboutParagraph>
            <AboutParagraph>
              Podczas imprez firmowych i wesel wystpowalimy m.in. z Kub
              Badachem, Danzelem, Darkiem Malejonkiem. Zrealizowalimy wesele
              Magorzaty i&nbsp;Radosawa Majdana.
            </AboutParagraph>
          </div>
        </AboutArticle>
      </AboutBox>
      <h2>Dowiadczenie</h2>
      <AboutBox>
        <RowArticle>
          <AboutParagraph $modified>
            Grupa bya nagradzana i&nbsp;doceniana przez bran weseln,
            otrzymujc:
          </AboutParagraph>
          <ul>
            <li>
              status Partnera Honorowego Polskiego Towarzystwa Konsultanw
              lubnych (2019),
            </li>
            <li>
              Certyfikat Partnera Honorowego Polskiego Towarzystwa Konsultanw
              lubnych (2017),
            </li>
            <li>
              Certyfikat Partnera Honorowego Polskiego Towarzystwa Konsultanw
              lubnych (2016).
            </li>
          </ul>
          <AboutSection $wrap>
            <CertImg src={Cert2} alt="" width={200} height={275} />
            <CertImg src={Cert3} alt="" width={200} height={275} />
            <CertImg src={Cert1} alt="" width={200} height={120} />
          </AboutSection>
        </RowArticle>

        <RowArticle>
          <AboutParagraph $modified>Zaufali nam m. in. :</AboutParagraph>
          <AboutSection $wrap>
            <LidlLogo />
            <CasionosLogo />
            <OnTracoLogo />
            <WarsawLogo />
            <img src={SarimLogo} width={200} height={200} alt="SARIMlogo" />
          </AboutSection>
        </RowArticle>
      </AboutBox>
      <ContactBox />
    </MainContentBox>
  );
};
